  margin: auto;
  width: 200px;
  height: 200px;
  animation-name: LoadingScreen_rotateAnim__1Q49Q;
  -o-animation-name: LoadingScreen_rotateAnim__1Q49Q;
  -ms-animation-name: LoadingScreen_rotateAnim__1Q49Q;
  -webkit-animation-name: LoadingScreen_rotateAnim__1Q49Q;
  -moz-animation-name: LoadingScreen_rotateAnim__1Q49Q;
  animation-duration: 0.4s;
  -o-animation-duration: 0.4s;
  -ms-animation-duration: 0.4s;
  -webkit-animation-duration: 0.4s;
  -moz-animation-duration: 0.4s;
  animation-iteration-count: infinite;
  -o-animation-iteration-count: infinite;
  -ms-animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-timing-function: linear;
  -o-animation-timing-function: linear;
  -ms-animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
  -moz-animation-timing-function: linear;
}
.LoadingScreen_loader__2RHMm div {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 2px solid rgb(34,78,189);
  position: absolute;
  top: 4px;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.LoadingScreen_loader__2RHMm div:nth-child(odd) {
  border-top: none;
  border-left: none;
}
.LoadingScreen_loader__2RHMm div:nth-child(even) {
  border-bottom: none;
  border-right: none;
}
.LoadingScreen_loader__2RHMm div:nth-child(2) {
  border-width: 4px;
  left: 0px;
  top: -8px;
  width: 24px;
  height: 24px;
}
.LoadingScreen_loader__2RHMm div:nth-child(3) {
  border-width: 4px;
  left: -2px;
  top: 6px;
  width: 36px;
  height: 36px;
}
.LoadingScreen_loader__2RHMm div:nth-child(4) {
  border-width: 6px;
  left: -2px;
