  height: 48px;
}
.LoadingScreen_loader__2RHMm div:nth-child(5) {
  border-width: 6px;
  left: -2px;
  top: 8px;
  width: 64px;
  height: 64px;
}
.LoadingScreen_loader__2RHMm div:nth-child(6) {
