  height: 100px;
}


@keyframes LoadingScreen_rotateAnim__1Q49Q {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}

@-webkit-keyframes LoadingScreen_rotateAnim__1Q49Q {
  from {
    -webkit-transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
  }
}
